.App {
  text-align: center;
}

html {
	height:100%;
}

body,
textarea,
input,
select,
button {
  	font-family: 'Raleway', Fallback, sans-serif;
  	outline:none;
}
button#downloadbutton {
    margin-top: -4%;
    float: right;
    margin-right: 1%;
    background: #376c82;
    color: white;
    border: 1px solid transparent;
    border-radius: 4px;
    height: 150%;
    width: 20%;
    cursor: pointer;
}

.switch {
    height:100%
}

body {
    overflow-y:hidden;
    height:100%;
	margin: 0;
  	padding: 0;
}

div#loaderDiv {
    top: 50%;
    left: 40%;
    position: absolute;
    z-index: 99999;
}

img#loader {
    width: 65%;
}

div#grades {
    float: left;
    margin-right: -16%;
    font-weight: bold;
    margin-top: -4%;
}

select#studDropdown,
select#assignmentsDropdown{
    width: 50%;
    height: 70%;
    border-bottom: 0px;
}

.borderProps {
    border: 1px solid lightgray;
    border-radius: 4px;
}

.gradeClass {
	color:red;
	font-size: 13px;
}

hr {
	border-color: #e9e8ea87;
}

div#feedbackSubmitDiv {
    width: 44%;
    height: 94%;
    margin-left: 1%;
}

td, th {
    padding: 5px;
}

div#leftPane {
    height: 91%;
    width: 25%;
    top: 7%;
    margin-left: 5px;
    position: absolute;
}

div#studentsList {
    height: 7%;    
}

div#rubric {
    height: 94%;
    margin-top: 10%;
}

div#rightPane {
    margin-left: 26%;
    height: 98%;
    width: 74%;
    top: 7%;
    position: absolute;
    display: -ms-flexbox;
    display: flex;
}

h4, h3 {
    margin-bottom: 0px;
    margin-top: 5px;
    font-weight:500;
}

div#mainDiv {
    width: 100%;
    height: 93%;
    position: absolute;
    display: -ms-flexbox;
    display: flex;
}

div#codePane {
    width: 55%;
}

div#app {
    display: -ms-flexbox;
    display: flex;
}

div#topPane {
    width: 100%;
    height: 6%;
    background: #376c82;
    color: white;
    position: absolute;
}

.guidelinesData {
    width: 73%;
    margin-bottom: 7%;
    height: 23px;
	background:white;
    cursor: not-allowed;
}

button.more {
    background: transparent;
    border: 0px;
    color: blue;
    cursor: pointer;
}

div#block {
    margin-left: -5px;
}

button.more:visited {
    border:0px;
}

div#deductions {
    width: 98%;
    margin-bottom: 1%;
    height:100%;
}

div#deductionsTableDiv {
	overflow-y:auto;
	height: 79%;
}

div#initialDiv {
    width: 100%;
    height: 100%;
    text-align: center;
    margin-top: 30%;
    color: red;
}

table#deductionsTable {
    font-size: 13px;
}

div#feedback {
    height:68%;
    margin-top: 3%;
}

div#deductionsMainDiv{
  height: 35%;
}

div#feedbackDiv {
	width:98%;
	height:95%;
}

div#submit {
    /*height: 50%;*/
    margin-top: 1%;
}

div#guidelines {
    overflow-y: auto;
    height: 91%
}

button#submitButton {
    background: #376c82;
    color: white;
    border: 1px solid transparent;
    border-radius: 4px;
    height: 10%;
    width: 20%;
    cursor: pointer;    
}

textarea#feedback {
    font-size: 12px;
    resize: none;
    cursor: not-allowed;
    background: transparent;
    resize: none;
    width: 99%;
    border:0px;
	height: 85%;
	margin-top: 0px;
}

input#submit {
    float: right;
}

input[type="submit"] {
    background: #007cff;
    color: white;
    height: 50%;
    width: 6%;
    border-style: hidden;
    border-radius: 10px;
    cursor: pointer;
}

textarea#code:hover,
textarea#feedback:hover {
  border-color:#2e85c3;
}

::-webkit-scrollbar {
    width: 8px;
    height:8px;
}
 
::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
    border-radius:10px;
}
 
::-webkit-scrollbar-thumb {
  background-color: gray;
  outline: 1px solid slategrey;
  border-radius:10px;
}


